











































import { Component, Vue } from "vue-property-decorator";
import { State, Action } from 'vuex-class';

@Component({})
export default class Header extends Vue {

    @State("pc")
    pc!: number;

    @State("path")
    path!: string;

    expand = false;

    @Action("savePath")
    savePath!:Function;

    mounted() {
        this.savePath(this.$route.path);
    }

    push(value) {
        if (this.path !== value) {
            this.savePath(value);
            this.$router.push(value);
        }
    }
}
